<!--走马灯/轮播图-->
<template>
  <div class="banner">
    <el-carousel id="el-carousel"
                 :interval="5000"
                 arrow="always">
      <el-carousel-item v-for="item in items"
                        :key="item">
        <img :src="item.src" />
      </el-carousel-item>
    </el-carousel>
  </div>
</template>

<script>
export default {
  name: "Carousel",
  data() {
    return {
      msg: "",
      items: [
        { src: require("../assets/banner01.jpg") },
        { src: require("../assets/banner02.jpg") },
        { src: require("../assets/banner03.jpg") },
        { src: require("../assets/banner04.jpg") },
        { src: require("../assets/banner05.jpg") },
        { src: require("../assets/banner06.jpg") },
      ],
    };
  },
  mounted() {
  },
  methods: {
  },
};
</script>

<style>
.banner img{
  width: 100%;
  height: auto;
}
</style>



