<!--公司-->
<template>
  <div style="width: 100%">
    <el-row>
      <el-col :span="15" style=" float: left;">
        <el-card shadow="never" class="box-card">
        <div slot="header" class="clearfix companyHeader">
          <span style="float:left;">公司简介</span>
          <!-- <el-button style="float: right; padding: 3px 0" type="text">操作按钮</el-button> -->
        </div>
        <div class="companyInfo">
          <div class="companyInfoLeft"><img src="../assets/xdc.jpg" class="image"></div>
          <div class="companyInfoRight"><span>　 湖北成特系统工程有限公司专业从事开发生产与经营可靠不间断电源UPS产品,提供专业的设计方案,UPS电源,UPS蓄电池,精密空调,环境监控等,专业从事数据中心集成和机房建设的企业. UPS渠道分销  湖北成特系统工程有限公司专业从事开发、生产与经营可靠的、安全易管理的不间断电源（UPS）产品  我们的成功源自于不懈地提升产品品质，并以优质、高效的服务帮助客户构建安全的电力基础...</span></div>
        </div>
        </el-card>
      </el-col>

      <el-col :span="8" style="float: right;">
        <el-card shadow="never">
          <el-tabs v-model="activeName" @tab-click="handleClick">
            <el-tab-pane label="工程案例" name="first">工程案例</el-tab-pane>
            <el-tab-pane label="新闻活动" name="second">新闻活动</el-tab-pane>
            <el-tab-pane label="技术支持" name="third">技术支持</el-tab-pane>
          </el-tabs>
        </el-card>
      </el-col>
  </el-row>
  </div>
</template>

<script>
export default {
  name: "Card",
  data() {
     return {
        activeName: 'first'
      };
  },
  methods: {
      handleClick(tab, event) {
        console.log(tab, event);
      }
    }
};
</script>

<style>
  .el-card {
    height: 200px;
  }
  .el-card__header {
    padding: 20px;
    border-bottom: 1px solid #dcdcdc;
  }
  .text {
    font-size: 14px;
  }

  .item {
    margin-bottom: 18px;
  }

  .clearfix:before,
  .clearfix:after {
    display: table;
    content: "";
  }
  .clearfix:after {
    clear: both
  }

  .box-card {
    /* height: 100%; */
    /* height: 300px; */
  }

  .companyHeader span{
    color: #666;
    font-size: 18px;
    font-weight: bold;
  }

  .el-card {
    border-top: 1px solid #dcdcdc;
  }

  .companyInfoLeft {
    float:left;
     width:40%;
  }

  .companyInfoLeft img{
    width: 260px;
    height: 160px;
    border: none;
    vertical-align: middle;
    margin: 0;
    
  }

  .companyInfoRight {
    float:right; 
    width:58%;
  }
</style>



