<!--卡片-->
<template>
  <div style="width: 100%">
    <div class="imgShow">
      <el-row style="height:100px;"> 
        <el-col :span="4" style="height:100px;">
          <el-card shadow="hover">
            <img src="../assets/ups.jpg" class="image" shadow="hover">
              <p shadow="hover">UPS电源</p>
          </el-card>
        </el-col>
        <el-col :span="4">
          <el-card shadow="hover">
            <img src="../assets/xdc.jpg" class="image">
              <p>蓄电池</p>
          </el-card>
        </el-col>
        <el-col :span="4">
          <el-card shadow="hover">
            <img src="../assets/wyq.jpg" class="image">
              <p>稳压器电源</p>
          </el-card>
        </el-col>
        <el-col :span="4">
          <el-card shadow="hover">
            <img src="../assets/kongtiao.jpg" class="image">
              <p>精密空调</p>
          </el-card>
        </el-col>
        <el-col :span="4">
          <el-card shadow="hover">
            <img src="../assets/sjzx.jpg" class="image">
              <p>数据中心业务</p>
          </el-card>
        </el-col>
        <el-col :span="4">
          <el-card shadow="hover">
            <img src="../assets/jfgc.jpg" class="image">
              <p>机房工程</p>
          </el-card>
        </el-col>
      </el-row>
    </div>
    <div class="clear"></div>
  </div>
</template>

<script>
export default {
  name: "Card",
  data() {
    return {
      msg: "",
    };
  },
};
</script>

<style>
.clear {
  clear: both;
}
.CardTitle {
  float: left;
  font-size: 30px;
  margin: 20px 0;
}
.clear {
  clear: both;
}
.el-card {
  margin-bottom: 30px;
  padding: 20px 0;
}
.card-main {
  font-size: 25px;
  color: #215eba;
}
.card-content {
  color: #68707b;
  margin-top: 30px;
}
.clearfix:after {
  content: "";
  display: block;
  clear: both;
  visibility: hidden;
}
/* .el-card_body {
  height: 120px;
  width: 120px;
} */

.imgShow .el-card {
  border:none;
  margin-left: 5px;
  cursor: pointer;
}
.imgShow .el-card img{
  z-index: -99;
  height: 100%; 
  width: 100%;
  cursor: pointer;
}

</style>



