<!--导航条-->
<template>
  <div class="navList" style="width: 100%">
    <el-menu 
             class="el-menu-demo"
             mode="horizontal"
             background-color="#215EBA"
             text-color="#FFFF"
             style="width:100%"
             @select="handleSelect">
      <div class="navItems">
        <el-menu-item index="1">首页</el-menu-item>
        <el-menu-item index="2">关于成特</el-menu-item>
        <el-menu-item index="3">新闻动态</el-menu-item>
        <el-menu-item index="4">产品与方案</el-menu-item>
        <el-menu-item index="5">服务支持</el-menu-item>
        <el-menu-item index="6">融入成特</el-menu-item>
        <el-menu-item index="7">工程案例</el-menu-item>
        <el-menu-item index="8">技术支持</el-menu-item>
        <!-- <el-menu-item index="4">更多</el-menu-item> -->
        <!-- <el-submenu index="4">
          <template slot="title">更多</template>
          <el-menu-item index="4-1">选项1</el-menu-item>
          <el-menu-item index="4-2">选项2</el-menu-item>
          <el-menu-item index="4-3">选项3</el-menu-item>
        </el-submenu> -->
      </div>
    </el-menu>
    <div class="line"></div>
  </div>
</template>

<script>
export default {
  name: "Nav",
  data() {
    return {
    };
  },
};
</script>

<style scoped>
.navList {
    /* max-width: 100%;
    min-width: 1280px; */
    overflow: hidden;
  }
.el-menu {
    margin-top: 30px;
    text-align: center;
}
.el-menu-item,
.el-submenu {
    display: inline-block;
    /* font-size: 20px; */
}
.navItems {
    text-align: center;
    height: 100px;
    max-width: 100%;
    /* min-width: 1280px; */
    /* background: red; */
    /* width: 1500px; */
    /* float: left; */
    /* margin-left: 50px; */
}
.el-menu-item{
    line-height: 100px;
    font-size: 18px;
    height: 100px;
}
</style>



